<template>
    <div class="search ">
      <div class="wrapper">
        <router-link to="/">
          <img src="../../static/image/logo.png"  title="加盟指南" class="logo">
        </router-link>
        <div>
          <el-input placeholder="请输入内容" v-model="keyword" class="input-with-select" clearable>
            <i slot="prefix" class="el-input__icon el-icon-search"></i>

              <el-button slot="append" >
                <router-link :to="'/list?keyword='+keyword">搜索</router-link>
              </el-button>

          </el-input>
          <div class="tag">
            <span class="name">热门搜索：</span>
            <span  v-for="item in keywordList" :key="'keyword'+item.id" @click="search(item.word)">{{item.word}}</span>
          </div>
        </div>
        <router-link to="/evaluate">
          <img src="../../static/image/evaluate.png" alt="" class="evaluate">
        </router-link>
      </div>

    </div>
</template>


<script>
  import commonApi from '@/api/common-api'
    export default {
        name: "search",

      data(){
        return {
          keyword:'',
          keywordList: []
        }
      },
      watch:{
        $route(to,from){
          console.log("ro.path=========",to.query);
          this.keyword = to.query.keyword;

        }
      },
      created(){
          this.keyword = this.$route.query.keyword;
          this.getKeyList();
      },
      methods: {
          getKeyList: function(){
              commonApi.getKeywordList().then(data =>{
                this.keywordList = data.hot;
              });
          },
        search:function(keyword){
            this.$router.push('/list?keyword='+keyword)
        }
      }
    }
</script>

<style scoped>
    .search{
      background-color: #fff;

    }
    .search .wrapper{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 128px;

    }
    .logo{
      width: 194px;
      height: 68px;
    }
    .evaluate{
      width: 200px;
      height: 59px;

    }
    .el-input {
      width: 430px;
    }
    .tag{
      font-size: 12px;
      color: #808080;
      text-align: start;
      margin-top: 10px;
    }
    .tag span{
      margin-right: 20px;
      cursor: pointer;
    }
    .tag .name{
      color: #1a1a1a;
      font-weight: bold;
      margin-right: 0;
      cursor: default;
    }
</style>



