import httpUtils from 'config/index'

class personalService {

  /**
   * 获取个人中心数据
   * @returns {*}
   */
  getData() {
    return httpUtils.get(`/index/user/count`)
  }

  /**
   * 获取消息列表
   * @param params
   * @returns {*}
   */
  getMessageList(params) {
    return httpUtils.get(`/index/consult/get_push_consult`,params)
  }

  /**
   * 查看消息详情
   */
  getDetail(params){
    return httpUtils.get(`/index/consult/read_Notify`,params)
  }
  /**
   * 获取我的评论列表
   * @param params
   * @returns {*}
   */
  getConsultList(params){
    return httpUtils.get(`/index/notice/like_notice`,params)
  }

  /**
   * 获取我的关注列表
   * @param params
   * @returns {*}
   */
  getFocusList(params){

    return httpUtils.get(`/index/item/item_list`,params)
  }

  /**
   *  获取项目列表
   */
  getProductList(params){
    return httpUtils.get(`/index/item/item_list`,params)
  }

  /**
   * 获取收藏的文章列表
   */
  getArticleList(params){
    return httpUtils.get(`/index/article/article_list`,params)
  }

  /**
   * 取消收藏/关注
   * type: 1取消关注项目2取消收藏项目3取消收藏文章
   */
  cancelCollect(params) {
    return httpUtils.get(`/index/follow/remove_follow`,params)
  }

  /**
   * 提交咨询
   */
  submitConsult(params){
    console.log("========",params);
    return httpUtils.post(`/index/consult/add_consult`,params)
  }

  /**
   * 获取留言列表
   */
  getLeaveList(params){
    return httpUtils.get(`/index/consult/get_my_consult`,params)
  }

  /**
   * 提醒
   */
  urge(params){
    return httpUtils.get(`/index/consult/com_urge`,params)
  }

  /**
   * 企业中心 - 投资者管理列表/留言管理
   * type 0咨询1留言
   */
  getInvertorManage(params){
    return httpUtils.get(`/index/consult/get_consult`,params)
  }

  /**
   * 企业中心-获取个人资料
   */
  getUserInfo(){
    return httpUtils.get('/index/comment/com_list');
  }
  /**
   * 提交建议
   */
  submitOpinion(params){
    return httpUtils.post('/index/aboutus/propose',params);
  }

  /**
   * 获取咨询或者留言详情
   * @param params
   * @returns {*}
   */
  getConsultDetail(params){
    return httpUtils.post('/index/consult/get_consult_detail',params);
  }

  /**
   * 添加跟进或者回复
   */
  addConsult(params){
    return httpUtils.post('/index/consult/add_consult_log',params);

  }

  /**
   * 删除留言OR 恢复
   *
   */
  delConsult(params){
    return httpUtils.get('/index/consult/del_consult',params);

  }
}
export default new personalService()



